// Font declaration file

@font-face
  font-weight: 100
  font-family: 'Roboto'
  font-style: normal
  src: local('Roboto Thin'), local('Roboto-Thin')
  src: url('../../assets/fonts/roboto/Robotothin.woff2') format('woff2')
  src: url('../../assets/fonts/roboto/Robotothin.woff') format('woff')
  src: url('../../assets/fonts/roboto/Robotothin.ttf') format('truetype')

@font-face
  font-weight: 100
  font-family: 'Roboto'
  font-style: italic
  src: local('Roboto Thin Italic'), local('Roboto-ThinItalic'),
